<template>
</template>
<script>
export default {
  data () {
    return {
      product: {}
    }
  },
  route: {
    data (transition) {
      this.$http.get('http://jiancan.me/api/u1/products/one.json', { product_id: this.$route.params.product_id }).then(function (response) {
        this.$set('product', response.data)
      }, function (response) {
        this.$dispatch('response-msg', response)
      })
    }
  }
}
</script>



